.item {
  position: relative;
  width: 64px;
  height: 64px;
  background-color: #131316;
  border-radius: 100px;
  margin-left: -16px;
}
.item:first-of-type {
  z-index: 7;
  margin-left: 0;
}

.item:nth-child(2) {
  z-index: 6;
}

.item:nth-child(3) {
  z-index: 5;
}
.item:nth-child(4) {
  z-index: 4;
}
.item:nth-child(5) {
  z-index: 3;
}
.item:nth-child(6) {
  z-index: 2;
}

.item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 100px;
  padding: 2px;
  background: linear-gradient(to right, #801ab2, #4c4cff);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.image {
  width: 110px;
  margin-left: -22px;
  margin-top: 5px;
}
